.cer {
    overflow-x: hidden !important;
    .iso {
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        .iso-img {
            display: flex;
            justify-content: center;
        }
        .iso-col {
            font-weight: 600;
            font-size: 40px;
            text-align: center;
            color: #6b6d85;
        }
        .btn-iso {
            width: 256px;
            height: 70px;
            border-radius: 6px;
            background: #ff5951;
            position: relative;
            left: 50%;
            transform: translateX(-50%);
            border-color: transparent;
            .text {
                display: inline-block;
                font-weight: 700;
                font-size: 20px;
                color: #fff;
                position: relative;
                left: 5px;
                top: 5px;
            }
        }
    }
}

@include media-breakpoint-down(lg) {
    .cer {
        position: relative;
        top: 100px;
        overflow-x: hidden !important;
        max-width: 100%;
        .iso {
            overflow-x: hidden !important;
            height: initial;
            max-width: 100%;
            display: block;
            .iso-col {
                font-size: 24px;
            }
            .iso-img{
                border: none !important;
            }
        }
        .iso::after {
            content: "";
            display: block;
            height: 200px;
        }
    }
}
