.ana-sayfa {
    overflow-x: hidden !important;

    .abs-bar2 {
        width: 100%;
        position: absolute;
        background: #ff5951;
        height: 44px;
        border: none;
    }
    .abs-bar3 {
        width: 100%;
        position: absolute;
        background: #f4f4f4;
        height: 88px;
        border: none;
        top: -44px;
        z-index: -1;
    }
    .products {
        position: relative;
        z-index: 999;

        .products-list {
            height: 100%;
            min-height: 760px;
            background: #ff5951;
            position: relative;

            .kasdir {
                position: absolute;
                top: 50%;
                // width: 70%;
                // height: 70%;
                transform: translate(50%, -50%);
            }
        }

        .products-details-sub {
            display: block;
            position: relative;
            height: 100%;
            top: 100px;
            .text-container {
                position: relative;
                display: flex;
                flex-direction: column;
                justify-content: space-evenly;

                .bottom-border {
                    background: transparent;
                    position: relative;
                    left: -20%;
                    z-index: -1 !important;
                    height: 2px;
                    width: calc(100% + 50%);
                    background: linear-gradient(173deg, rgba(216, 215, 217, 0) 0%, rgba(149, 144, 152, 1) 100%);
                    margin-bottom: 20%;
                    margin-top: 20%;
                }

                .title {
                    font-weight: 600;
                    font-size: 40px;
                    color: #6b6d85;
                }

                .description {
                    font-family: $font-family-sans-serif-2;
                    font-size: 20px;
                    line-height: 35px;
                    color: #9595aa;
                }
            }
        }
    }

    .capacity {
        background: #2e2c65;
        padding-top: 160px;
        padding-bottom: 220px;
        margin-bottom: $font-size-sm * -1;

        .title {
            margin-top: 50px;
            width: 100%;
            height: 80px;
            font-weight: 600;
            font-size: 40px;
            color: #fff;
        }

        .description {
            font-family: $font-family-sans-serif-2;
            font-size: 20px;
            line-height: 35px;
            color: #9595aa;
            max-width: 729px;
        }

        .img {
            img {
                border-bottom-right-radius: 6.25rem;
            }
        }
    }

    .mavi {
        width: 100%;
        position: relative;
        max-height: 800px;
        min-height: 426px !important;
        .mavi-img {
            .settings {
                position: absolute;
                // width: 90%;
                // left: 20%;
                top: 20%;
                .title {
                    font-weight: 600;
                    font-size: 50px;
                    text-align: left;
                    color: #fff;
                }
                .box {
                    width: 100%;
                    height: 137px;
                    border-radius: 6px;
                    background: transparent;
                    border: 1px solid #91d2f5;
                    .text {
                        font-weight: 600;

                        font-size: 1.375rem;
                        line-height: 30px;
                        text-align: center;
                        color: #fff;
                    }
                }
                .box:hover {
                    background-color: #ffff;
                    .text {
                        color: #2e2c65;
                    }
                }
            }
            img {
                width: 100%;
                max-height: 800px;
                min-height: 426px !important;
            }
        }
    }
    .isoAnaSayfa {
        background-color: #f4f4f4;
    }
}

@include media-breakpoint-down(xl) {
    .ana-sayfa {
        .mavi {
            .mavi-img {
                .settings {
                    width: 100%;
                    left: 10%;
                    top: 10%;
                    .box {
                        // height: 100px;
                    }
                }
            }
        }
        .capacity {
            text-align: center;
            margin-bottom: 0;
            .title {
                margin-top: 25px;
                margin-bottom: 40px;
            }
            .description {
            }
        }
        button {
            position: relative;
            left: 50%;
            transform: translateX(-50%);
        }
    }
}
@include media-breakpoint-down(lg) {
    .anaSayfa {
        .absBar,
        .abs-bar2 {
            display: none !important;
        }
    }
    .products {
        .title {
            min-width: 300px;
        }
        .products-details-sub {
            position: relative;
            left: 50%;
            margin-top: 0 !important;
            top: 0 !important;
            .text-container {
                width: 100%;
                position: relative;
                left: 50%;
                transform: translateX(-50%);
                width: 100%;
                top: 0 !important;
                height: 500px !important;
                justify-content: space-around;
                align-items: center;
                .text:first-of-type {
                    padding-bottom: 30px;
                    border-bottom: 1px solid #f0f0f0;
                }
                .bottom-border {
                    display: none;
                }
            }
        }
    }
}

@include media-breakpoint-down(md) {
    .ana-sayfa {
        .products {
            top: 0 !important;
            margin: 0 !important;
            .products-list {
                max-height: 422px;
                min-height: 422px;
                height: 422px;
                .text-container {
                    top: 0 !important;
                    margin: 0 !important;
                }
            }
        }
    }
}
