.products-details {
    position: relative;
    margin-top: 100px;
    overflow-x: hidden !important;
    .title {
        display: flex;
        justify-content: space-between;
        margin-bottom: 75px;

        div {
            height: 65px;
            font-weight: 600;
            font-size: 40px;
            line-height: 50px;
            text-align: left;
            color: #6b6d85;
        }

        a {
            font-size: 16px;
            letter-spacing: 0.13em;
            line-height: 30px;
            color: #bcbcbc;
            font-weight: 600;
        }
    }
    .prod-row {
        padding: 50px;

        .btn-c {
            width: 100%;
            height: 70px;
            border-radius: 6px;
            background: #ff5951;
            border: none;

            .text {
                font-weight: 400;
                font-size: 20px;
                color: #fff;
            }
        }
        .title-row {
            width: 545px;
            height: 65px;
            font-weight: 600;
            font-size: 40px;
            line-height: 50px;
            text-align: left;
            color: #6b6d85;
            display: flex;
            align-items: center;
            margin-bottom: 36px;
        }
        .description-row {
            font-family: $font-family-sans-serif-2;
            font-weight: 400;
            font-size: 20px;
            line-height: 35px;
            color: #848484;
        }
    }
    .prod-row:not(:last-of-type) {
        border-bottom: 1px solid #dadada;
    }
}

@include media-breakpoint-down(lg) {
    .products-details {
        padding-left: 28px;
        padding-right: 28px;

        .title {
            margin-bottom: 0px;

            div {
                font-weight: 400;
                width: 227px;
                height: 100px;
                font-size: 30px;
                line-height: 40px;
            }
        }

        .prod-row {
            border: none !important;
            padding: 0;
            padding-top: 50px;

            .btn-c {
                max-width: 255px;
                min-width: 255px;
                position: relative;
                left: 50%;
                transform: translateX(-50%);
            }
            .title-row {
                font-weight: 400;
            }

            .description-row {
                font-size: 18px;
                line-height: 35px;
            }
        }
        .prod-row:not(:last-of-type):after {
            display: block;
            position: absolute;
            border-bottom: 1px solid #dadada;
            content: "";
            width: 100%;
            height: 1px;
            left: -2px;
        }
    }
}
