.card-products-details {
    .card-body {
        font-size: 24px;
        line-height: 35px;
        width: 100%;
        padding: 71px 56px;
        color: #6b6d85;
        border: 1px solid #d2d2d2;
        .text {
            font-weight: 600;
            max-width: 150px;
            position: relative;
            a {
                max-width: 10px;
                white-space: pre-wrap;
                text-decoration: none;
                color: #6b6d85;
            }
        }
    }
    .card-body:hover {
        background: #6b6d85;
        a {
            color: #fff;
        }
    }
}

@include media-breakpoint-down(lg) {
    .card-products-details {
        .card-body {
            position: relative;
            min-height: 126px;
            padding: 0;
            display: flex;
            align-items: center;
            justify-content: center;
            .details {
                .text {
                    max-width: initial;
                    white-space: nowrap;
                    position: relative;
                    top: -5px;
                }
            }
        }
    }
}
