.products-page {
    overflow-x: hidden !important;
    min-height: 640px;
    padding-top: 100px;
}

@include media-breakpoint-down(lg) {
    .products-page {
        padding-top: 50px;
        
        .products-content {

            div[class^="col-lg"]:not(:last-of-type) {
                
                .cardc {
                    border-bottom: 1px solid #f0f0f0;
                }
            }
         
            div[class^="col-lg"]{
                .cardc:hover {
           
                    background: #6b6d85;
           
                    a {
                        color: #fff;
                    }
                }

            }
        }
    }
}