.p-card {
    .card-body {
        font-size: 40px;
        line-height: 55px;
        width: 100%;
        padding: 92px 66px;
        color: #6b6d85;
        border: 1px solid #d2d2d2;
        .text {
            font-weight: 600;
            a {
                text-decoration: none;
                color: #6b6d85;
            }
            .description {
                font-family: $font-family-sans-serif-2;
                font-weight: 400;
                font-size: 18px;
                line-height: 35px;
                text-align: left;
                color: #9595aa;
            }
        }
    }
    .card-body:hover {
        padding: calc(100%+20px);
        background: #6b6d85;
        a {
            color: #fff;
        }
    }
}

@include media-breakpoint-down(lg) {
    .p-card {
        // padding: 0;
        .card-body {
            min-height: 250px;
            padding: 0;
            border: none;
        }
        .card-body {
        }
    }
}
