/*
 *
 * Common
 *
 */
* {
    outline: none !important;
}

a,
a:hover,
a:active,
a:focus,
a:visited {
    text-decoration: none;
}

em {
    font-style: normal;
}

.section-main {
    position: relative;
}

.bg {
    $opacity: 0.9;

    &-primary {
        background-color: $primary;
    }
}

.separator {
    width: 100%;
    height: 1px;
    display: inline-block;
    background-color: $border-color;

    &.separator-headline {
        position: relative;
        margin-top: 0;
        padding-top: 0;
        width: 100%;
        height: 1px;
        display: inline-block;
        background-color: $border-color;

        &:after {
            display: block;
            content: "";
            position: absolute;
            left: 0;
            bottom: 0;
            height: 1px;
            width: 60px;
            background-color: $primary;
        }
    }

    &.separator-line {
        position: relative;
        margin-top: 0;
        padding-top: 0;
        width: 100%;
        height: 1px;
        display: inline-block;
        background-color: $border-color;
    }
}

.page-content {
    // Mobile
    font-size: $font-size-base;

    img {
        max-width: 100%;
        width: auto;
    }

    .content-img {
        margin-bottom: $grid-gutter-width;

        img {
            display: block;
            max-width: 100%;
            width: 100%;
        }
    }

    .content-text {
        margin-top: $grid-gutter-width;
    }

    p,
    .content-text {
        text-align: justify;
    }

    p,
    ul,
    ol {
        margin-bottom: $grid-gutter-width;
    }

    @include media-breakpoint-up(md) {
    
        // Desktop

        img {
            margin-bottom: $grid-gutter-width;
        }

        .content-img {

            img {
                width: auto;
            }

            &.pull-left {
                float: left;
                margin-left: auto;
                margin-right: $grid-gutter-width;
            }

            &.center {
                img {
                    margin-left: auto;
                    margin-right: auto;
                }
            }

            &.pull-right {
                float: right;
                margin-left: $grid-gutter-width;
                margin-right: auto;
            }

            &.stretch {
                img {
                    width: 100%;
                }
            }

            &.content-img-sm {
                width: (3 / $grid-columns) * 960 - 1 * $grid-gutter-width;
            }
            &.content-img-md {
                width: (4 / $grid-columns) * 960 - 1 * $grid-gutter-width;
            }
            &.content-img-lg {
                width: (5 / $grid-columns) * 960 - 1 * $grid-gutter-width;
            }
        }
    }
}
