.iso {
    height: 350px;
    .iso-content {
        position: relative;
        top: 50%;
        transform: translateY(-50%);
        img {
            max-width: 340px;
            max-height: 227px;
            min-width: 162px;
            min-height: 108px;
            width: 100%;
            height: 100%;
        }
        div[class^="col-"]:not(:last-of-type) {
            border-right: 1px solid #e7e7e7;
        }
    }
}
