.contact {
    overflow-x: hidden !important;
    margin-top: 100px;
    .title-bar {
        // margin-bottom: 80px;
        height: 65px;
        font-size: 40px;
        line-height: 50px;
        color: #6b6d85;
        display: flex;
        justify-content: space-between;
        font-weight: 600;
        .right-side {
            width: 100%;
            max-width: 545px;
        }
    }
    .iletisim {
        font-family: $font-family-sans-serif-2;
        font-weight: 400;
        font-size: 20px;
        line-height: 35px;
        color: #9595aa;
        display: flex;
        flex-direction: column;
        height: 100%;
    }
    form {
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        align-content: space-around;
        font-size: 20px;
        font-family: $font-family-sans-serif-2;
        line-height: 35px;

        input {
            color: #9595aa !important;
            border: 1px solid #dcdcdc;
            margin-bottom: 10px;
            height: 60px;
        }
        input[type="checkbox"] {
            height: 35px;
            width: 35px;
        }
        input[type="submit"] {
            width: 256px;
            height: 70px;
        }
        input[type="submit"] {
            color: white !important;
        }
        textarea {
            color: #9595aa !important;
            margin-bottom: 10px;
        }
        .check-box-text {
            font-family: $font-family-sans-serif-2;
            font-weight: 400 !important;
            font-size: 20px;
            line-height: 35px;
            color: #9595aa;
            padding: 0 !important;
            display: flex;
            align-items: center;
        }
    }
}
@include media-breakpoint-only(lg){
    .contact{
        form {
        .scale-class{
            transform: scale(0.75,0.9);
            margin-left: -15px;
        }
        }
    }
}
@include media-breakpoint-down(lg) {
    .contact {
        margin-top: 35px;
        padding-left: 20px;
        padding-right: 20px;
        .title-bar {
            font-size: 30px;
            line-height: 40px;
        }
        .iletisim {
            width: 227px;
        }
        .form-title {
            margin-top: 35px;
            margin-bottom: 50px;
            width: 227px;
            font-weight: normal;
            font-size: 30px;
            line-height: 40px;
            text-align: left;
            color: #6b6d85;
            font-weight: 600;
        }
        form {
            input {
                margin-bottom: 20px;
            }
            .scale-class {
                transform: scale(0.9);
                margin-bottom: 50px;
                display: inline-block;
                position: relative;
                left: 50%;
                transform: translateX(-50%);
            }
            input[type="submit"] {
                margin-bottom: 50px;
                position: relative;
                left: 50%;
                transform: translateX(-50%);
            }
            .check-box-text{
                white-space: nowrap;
            }
        }
    }
}
