.products-category {
    overflow-x: hidden !important;
    height: 839px;
    display: flex;
    align-items: center;
    .title-bar {
        display: flex;
        justify-content: space-between;
        font-weight: 600;
        font-size: 40px;
        line-height: 50px;
        color: #6b6d85;
        margin-bottom: 80px;
        a {
            color: #bcbcbc;
            font-weight: 400;
            font-size: 16px;
            letter-spacing: 0.13em;
            line-height: 30px;
        }
    }
}

@include media-breakpoint-down(lg) {
    .products-category {
        height: initial;
        .title-bar {
            font-size: 30px;
            line-height: 40px;
            margin-top: 50px;
            margin-bottom: 20px;
            div {
                width: 227px;
                height: 100px;
            }
            a {
                line-height: 35px;
                font-weight: 400;
                font-size: 16px;
                letter-spacing: 0.13em;
            }
        }
    }
   
}
