.caro {
  counter-reset: count 0;
  .carousel-control-next-icon,
  .carousel-control-prev-icon {
    display: none;
  }
  img {
    max-height: 800px;
  }

  .carousel-dark .carousel-indicators [data-bs-target] {
    background-color: #d8d8d8;
    width: 80%;
    color: rgb(0, 0, 0) !important;
  }
  .textCarousel {
    position: relative;
    counter-increment: count;
    font-family: "Gilroy-Regular";
    color: #fff;
    div {
      display: block;
      padding-left: 5px;
      font-size: 24px;
      span {
        display: block;
      }
      // max-width: 160px;
    }
    button.active {
      background-color: #ff5951 !important;
    }
  }

  .textCarousel::before {
    content: counter(count);
    display: inline-block;
    position: absolute;
    color: #fff;
    font-family: "Gilroy-Semibold";
    font-size: 30px;
    left: 0px;
  }
  .textCarousel:nth-child(-n + 9)::before {
    content: "0" counter(count);
  }
  .carousel-caption {
    position: absolute;
    top: 50%;
    left: 30%;
    transform: translate(-35%, -50%);
    text-align: left;
  }

  .numbering {
    display: inline-block;
    position: absolute;
    left: -20px;
    color: #fff;
    font-family: "Gilroy-Semibold";
  }
  .carousel-title {
    font-family: "Gilroy-Semibold";
    font-size: 80px;
    color: #fff;
    // min-width: 150px !important;
  }
  .carousel-description {
    font-family: "PlayfairDisplay-Regular";
    font-size: 20px;
    line-height: 35px;
    text-align: left;
    color: #fff;
    max-width: 650px;
  }
  .carousel-inner {
    .carousel-item {
      img {
        min-height: 800px;
      }
    }
  }
  .indepth {
    z-index: -1 !important;
  }

  .textCarousel > * {
    display: block !important;
    position: relative;
    left: 40px !important;
    text-align: left !important;
  }
}
@include media-breakpoint-down(lg) {
  .caro {
    .slick-slide {
      margin: 0 26px;
    }
    .textCarousel {
      div {
        // max-width: 125px !important;
        min-width: 140px;
        font-size: 18px;
        span {
          display: block;
        }
      }
      button {
        display: inline-block;
        position: relative;
        left: 40px !important;
        max-width: 100% !important;
      }
    }
    /* the parent */
    .slick-list {
      margin: 0 -26px;
    }
    .textCarousel::before {
      font-size: 20px;
      left: 20px;
    }
    .carousel-description {
      font-size: 16px;
      text-align: center;
      line-height: 1.2 !important;
      min-width: 300px;
      position: relative;
      left: 50%;
      transform: translateX(-50%) !important;
    }
    .carousel-caption {
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
    .carousel-title {
      font-size: 40px;
      width: 100%;
      min-width: 300px;
      text-align: center;
      // line-height: 25px;
      // height: 60px !important;
      position: relative !important;
      left: 50% !important;
      transform: translateX(-50%) !important;
    }
    .carousel-inner {
      .carousel-item {
        img {
          min-height: 400px !important;
        }
      }
    }
  }
}


@include media-breakpoint-down(md) {
  .caro {
    .carousel-caption {
      top: 125px;
      left: 50%;
      transform: translate(-50%,0);
    }
    .carousel-title{
      white-space: nowrap;
    }
    .slick-slide {
      margin: 0 0px;
    }
    .slick-list{
      margin: 0 -50px;
    }
    .textCarousel {
      div {
     min-width: 140px;
    //  max-width: 130px;
      }
      button {
        min-width: 120px;
        // max-width: 100% !important;
      }
    }
  }
}
