.banner {
    position: relative;
    height: 100%;
    width: 100%;
    min-width: 375px;
    max-height: 368px;
    .title {
        font-weight: 600;
        font-size: 60px;
        text-align: left;
        color: #ffff;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        left: 5%;
    }
    .breadCrumb {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        right: 5%;
    }
}
@include media-breakpoint-down(lg) {
    .banner {
        position: relative;
        // min-width: 428px;
        min-height: 167px;
        img {
            position: absolute;
            height: 100%;
        }
        .title{
            font-size: 30px;
        }
        .breadCrumb {
            top: 45%;
            right: initial;
            left: 5%;
            transform: translateY(0);
        }
    }
}

