.footer {
    overflow-x: hidden !important;
    .firstCol {
        min-height: 420px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        height: 100%;
        .content {
            .img {
                max-width: 292px;
                position: relative;
                left: 50%;
                transform: translateX(-50%);
                img {
                    width: 100%;
                    max-width: 200px;
                    max-height: 48px;
                }
            }
            .text {
                font-family: $font-family-sans-serif-2;
                font-size: 20px;
                line-height: 35px;
                color: #9595aa;
                max-width: 265px;
                margin-top: 40px;
            }
        }
    }
    .secondCol {
        min-height: 420px;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        .content {
            .title {
                font-weight: 600;
                font-size: 30px;
                line-height: 35px;
                color: #6b6d85;
                margin-bottom: 35px;
            }
            .iletisim {
                font-family: $font-family-sans-serif-2;
                font-size: 20px;
                line-height: 35px;
                color: #9595aa;
                max-width: 240px;
            }
        }
    }
    .thirdCol {
        font-weight: 600;
        font-weight: normal !important;
        font-size: 24px;
        color: #9595aa;
        & {
            min-height: 420px;
        }
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        .set {
            position: relative;
        }
        a {
            color: #9595aa;
            text-decoration: none;
        }
        .set:hover::before {
            content: "";
            width: 100px;
            height: 100px;
            background-image: url("../../temp/diagonal-arrow.png");
            background-repeat: no-repeat;
            position: absolute;
            top: 20px;
            left: -30px;
        }
        .set:hover {
            color: #2e2c65;
        }
    }
    .logo {
        min-height: 90px;
        img {
            object-fit: contain;
        }
    }
}
@include media-breakpoint-down(lg) {
    .footer {
        div[class^="col-"]:first-of-type {
            border: none !important;
        }
        .firstCol {
            min-height: 225px !important;
            .content {
                margin-top: 50px;
                margin-bottom: 30px;
                .img {
                    display: flex;
                    justify-content: center;
                }
                .text {
                    width: 290px;
                    text-align: center;
                    margin-top: 30px;
                }
            }
        }
        .secondCol {
            min-height: 225px !important;
            text-align: center;
        }
        .thirdCol {
            min-height: 225px !important;
        }
    }
}
