@font-face {
	font-family: "fonticon";
	src: url('../fonts/icon/fonticon.eot');
	src: url('../fonts/icon/fonticon.eot?#iefix') format('eot'),
		url('../fonts/icon/fonticon.woff') format('woff'),
		url('../fonts/icon/fonticon.ttf') format('truetype'),
		url('../fonts/icon/fonticon.svg#fonticon') format('svg');
}

@mixin ms-icon-styles {
	font-family: "fonticon";
    font-style: normal;
    font-weight: normal;
    speak: never;
    display: inline-block;
    text-decoration: inherit;
    text-align: center;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.ms-icon {
	line-height: 1;

	&:before {
		@include ms-icon-styles;
	}
}

@function ms-icon-char($filename) {
	$char: '';

	@if $filename == 'arrow-left' {
		$char: '\E001';
	}
	@if $filename == 'Rectangle-bar' {
		$char: '\E002';
	}

	@return $char;
}

@mixin ms-icon($filename, $insert: before, $extend: false) {
	&:#{$insert} {
		@if $extend {
			@extend .ms-icon;
		}
		content: ms-icon-char($filename);
	}
}

.ms-icon-arrow-left {
	@include ms-icon(arrow-left);
}
.ms-icon-Rectangle-bar {
	@include ms-icon(Rectangle-bar);
}
