.aboutUs {
    overflow-x: hidden;
    .pad2 {
        padding: 0 150px;
    }
    .description-section {
        .about-us-mask {
            .right-img {
                max-height: 737px;
                img {
                    border-bottom-right-radius: 100px;
                }
            }
        }
        .desc-one {
            margin-top: 75px;
            font-weight: 600;
            font-size: 30px;
            line-height: 50px;
            color: #6b6d85;
        }
        .desc-two {
            margin-top: 70px;
            font-family: $font-family-sans-serif-2;
            font-size: 20px;
            line-height: 40px;
            text-align: left;
            color: #848484;
        }
    }
    .orange {
        min-height: 260px;
        background: #ff5951;
        display: flex;
        justify-content: center;
        align-items: center;
        // position: relative;
        // left: 0px;
        // transform: translateX(-10%);
        .text {
            font-weight: 600;
            font-size: 40px;
            line-height: 50px;
            text-align: left;
            color: #fff;
        }
    }
    .after-orange {
        width: 100%;
        margin-top: 100px;
        .title {
            padding: 0;
            font-weight: 600;
            font-size: 2.5rem;
            line-height: 50px;
            color: #6b6d85;
            width: 100%;
            margin-bottom: 10px;
        }
        .description {
            font-family: $font-family-sans-serif-2;
            font-size: 20px;
            line-height: 35px;
            color: #848484;
        }
    }

    .no-pad-lg {
        padding: -1.5rem !important;
    }
}

@include media-breakpoint-down(lg) {
    .aboutUs {
        .container {
            padding: -1.54rem !important;
        }
        .no-pad {
            padding: 0 !important;
        }
        .no-pad-lg {
            padding: 0 !important;
        }
        .pad2 {
            padding: 0;
        }
        .description-section {
            .pad {
                padding: 0;
                .img {
                    width: 50%;
                    position: relative;
                    left: 50%;
                    transform: translateX(-50%);
                }
            }
            .desc-one {
                font-size: 24px;
                line-height: 40px;
            }
            .about-us-mask {
                padding: 0;
                margin-left: 0;
                .right-img {
                    padding: 0;
                    min-height: initial;
                    img {
                        width: 80%;
                        max-height: 1000px;
                        height: 700px;
                    }
                }
            }
        }
        .orange::before {
            position: absolute;
            top: -159px;
            height: 200px;
            width: 100%;
            background-color: #ff5951;
            content: "";
            z-index: -1;
        }
        .orange {
            padding: 0 !important;
            height: 400px;
            //    margin-top: -200px;
            position: relative;
            z-index: -1;
            .text {
                width: 50%;
                text-align: center;
            }
        }
        .after-orange {
            .title {
                display: block;
                position: relative;
                left: 50%;
                transform: translateX(-50%);
                font-size: 40px;
                line-height: 50px;
                width: 100%;
                max-width: 270px;
                margin-top: 50px;
            }
            .description {
                display: block;
                position: relative;
                left: 50%;
                transform: translateX(-50%);
                width: 100% !important;
                max-width: 270px;
            }
        }
    }
}
@include media-breakpoint-down(md) {
    .aboutUs {
        .description-section {
            .about-us-mask {
                .right-img {
                    img {
                        width: 100%;
                        height: 460px;
                    }
                }
            }
        }
    }
}
