.bar {
    .text {
        position: relative;
        a {
            font-weight: 500;
            font-size: 24px;
            line-height: 70px;
            color: #ababaa;
        }
        a:hover::before {
            content: "";
            width: 100px;
            height: 100px;
            background-image: url("../../temp/diagonal-arrow.png");
            background-repeat: no-repeat;
            position: absolute;
            top: 40px;
            left: -30px;
        }
        a:hover {
            color: #6b6d85;
        }
    }
}
