.crumb {
    color: #ffff;
    ul {
        list-style: none;
        padding: 0;
        li {
            float: left;
            padding-right: 5px;
            a {
                display: inline-block;
                color: white;
                text-decoration: none;
                text-align: center;
                font-family: $font-family-sans-serif-2;
                font-weight: 400;
                font-size: 20px;
                line-height: 35px;
            }
        }
        li:not(:first-of-type)::before {
            padding-right: 5px;
            content: " /";
            display: inline-block;
            font-size: 20px;
            line-height: 30px;
        }
    }
}

@include media-breakpoint-down(lg) {
    .crumb {
        ul {
            li {
                a {
                    font-size: 14px;
                }
            }
            li:not(:first-of-type)::before {
                font-size: 14px;
            }
        }
    }
}
